
#wrapper {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 1;
  overflow: hidden
}
#wrapper .refresh-loader {
  text-align: center;
  padding: 5px 0
}
#wrapper .refresh-loader i {
  font-size: 20px;
  color: #999;
  vertical-align: middle;
}
#wrapper .refresh-loader .icon-pulldown {
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
  display: inline-block;
}
#wrapper .refresh-loader .icon-pullup {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}
#wrapper .refresh-loader .icon-complete, #wrapper .refresh-loader .icon-error {
  font-size: 23px;
}
#wrapper .refresh-loader .text-default {
  vertical-align: middle;
  color: #999;
  font-size: 14px;
}
#wrapper .scroll-list {
  list-style: none;
  margin: 0;
  padding: 0
}
#wrapper .scroll-list .list-item {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}
#wrapper .infinite-loader {
  position: absolute;
  width: 100%;
  z-index: 10000;
  text-align: center;
  padding: 5px 0
}
#wrapper .infinite-loader .text-default {
  vertical-align: middle;
  font-size: 14px;
  color: #999;
}
#wrapper .spinner-default {
  display: inline-block;
  width: 18px;
  height: 18px;
  line-height: 28px;
  border-radius: 50%;
  vertical-align: middle;
  position: relative;
  border: 1px solid #999;
  -webkit-animation: loading-rotating ease 1.5s infinite;
          animation: loading-rotating ease 1.5s infinite
}
#wrapper .spinner-default:before {
  content: '';
  width: 6px;
  height: 6px;
  position: absolute;
  display: block;
  top: 0;
  left: 50%;
  margin-top: -3px;
  margin-left: -3px;
  background-color: #999;
  border-radius: 50%;
}
@-webkit-keyframes loading-rotating {
0%{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes loading-rotating {
0%{
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
100%{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}

@font-face {font-family: "iconfont";
  src: url(data:application/vnd.ms-fontobject;base64,); /* IE9*/
  src: url(data:application/vnd.ms-fontobject;base64,#iefix) format('embedded-opentype'), 
  url('data:application/x-font-woff;charset=utf-8;base64,') format('woff'),
  url(data:application/x-font-ttf;base64,) format('truetype'), 
  url(data:image/svg+xml;base64,#iconfont) format('svg'); /* iOS 4.1- */
}

.iconfont {
  font-family:"iconfont" !important;
  font-size:16px;
  font-style:normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-error:before { content: "\E727"; }

.icon-pulldown:before { content: "\E64A"; }

.icon-complete:before { content: "\E688"; }

